<template>
  <li class="hot-song" @click="playMusic(hotSong)">
    <div class="left" :class="num < 3 ? 'topNo' : 'oldNo'">
      {{ num < 9 ? "0" + (num + 1) : num + 1 }}
    </div>
    <div class="right">
      <div class="songLeft">
        <h3 :class="hotSong.id === nowSong?.id ? 'active' : ''">{{ hotSong.name }}</h3>
        <div class="author">
          <div v-if="hotSong.sq !== null" class="jl-sq"></div>
          <span>
            <span>
              {{ artistAll(hotSong.ar) }}
            </span>
            -
            {{ hotSong.al.name }}
          </span>
        </div>
      </div>
      <div class="play" v-if="hotSong.id === nowSong?.id">
        <div class="playing" v-for="(key, index) in 4" :key="index"></div>
      </div>
      <div class="jl-play" v-else></div>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    hotSong: Object,
    num: Number,
    nowSong: [Object],
  },
  methods: {
    playMusic: function (res) {
      return this.$emit("Updatd-song", res);
    },
    artistAll: function (arr) {
      let arrName = [];
      for (let i = 0; i < arr.length; i++) {
        arrName.push(arr[i].name);
      }
      return arrName.join("/");
    },
  },
};
</script>

<style lang="less" scoped>
.flex() {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.hot-song {
  padding: 10rem 0 0 0rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: relative;
  z-index: 1;
  .left {
    flex-shrink: 0;
    padding: 5rem;
    color: #9c9c9c;
    width: 40rem;
    text-align: center;
    font-size: 18rem;
    &.topNo {
      color: #d43c33;
    }
    &.oldNo {
      color: #9c9c9c;
    }
  }
}
.right {
  border-bottom: 1rem solid #ccc;
  width: calc(100% - 40rem);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 10rem;
  padding-bottom: 6rem;
  .songLeft {
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    h3 {
      width: calc(100% - 25rem);
      font-size: 17rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      &.active {
        color: #f00;
      }
    }
    .author {
      margin-top: 5rem;
      display: flex;
      align-items: center;
      .jl-sq {
        margin-right: 5rem;
        width: 15rem;
        height: 11rem;
        background-image: url(../assets/jingling.png);
        background-size: 180rem;
        background-position: 1rem 2rem;
      }
      span {
        font-size: 12rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        transform: scale(0.95);
        transform-origin: left;
        color: #797979;
      }
    }
  }
  .play {
    .flex();
    justify-content: space-around;
    width: 30rem;
    height: 30rem;
    padding: 3.5rem 3.5rem 3.5rem 0;
    @keyframes informationMove {
      0% {
        transform: rotateX(60deg);
      }
      50% {
        transform: rotateX(30deg);
      }
      100% {
        transform: rotateX(60deg);
      }
    }
    .playing {
      width: 10%;
      height: 100%;
      background-color: #f00;
      border-radius: 50rem;
      transform-origin: bottom;
      animation: informationMove 0.8s ease-in-out infinite;
      &:nth-child(1) {
        animation-delay: -0.1s;
      }
      &:nth-child(2) {
        animation-delay: -0.2s;
      }
      &:nth-child(3) {
        animation-delay: -0.3s;
      }
      &:nth-child(4) {
        animation-delay: -0.4s;
      }
    }
  }
  .jl-play {
    width: 30rem;
    height: 30rem;
    background-image: url(../assets/jingling.png);
    background-size: 179rem;
    background-position: 333rem 0rem;
  }
}
</style>
